import {
  SetCalloutStickerCodePayload,
  SetCalloutBackgroundPayload,
  SetCalloutSelectedPayload,
  SetMedataDataPayload,
  DeleteCalloutPayload,
  InsertCalloutPayload,
  ConvertKeysPayload,
} from './types';

export const INSERT_CALLOUT = Symbol('INSERT_CALLOUT');
export const DELETE_CALLOUT = Symbol('DELETE_CALLOUT');
export const SET_CALLOUT_STICKER_CODE = Symbol('SET_CALLOUT_STICKER_CODE');
export const SET_CALLOUT_BACKGROUND = Symbol('SET_CALLOUT_BACKGROUND');
export const SET_CALLOUT_SELECTED = Symbol('SET_CALLOUT_SELECTED');
export const SET_META_DATA = Symbol('SET_META_DATA');
export const CALLOUT_DATA_CHANGED = Symbol('CALLOUT_DATA_CHANGED');
export const CLEAR_CALLOUT_SELECTED = Symbol('CLEAR_CALLOUT_SELECTED');
export const TRANSFORM_CALLOUT = Symbol('TRANSFORM_CALLOUT');
export const CONVERT_KEYS_TO_CONTAINER = Symbol('CONVERT_KEYS_TO_CONTAINER');

export function insertCallout(_payload?: InsertCalloutPayload) {
  return {
    type: INSERT_CALLOUT,
    payload: _payload || { text: '' },
  };
}

export function transformCallout() {
  return {
    type: TRANSFORM_CALLOUT,
  };
}

export function deleteCallout(payload: DeleteCalloutPayload) {
  return {
    type: DELETE_CALLOUT,
    payload,
  };
}

export function setCalloutStickerCode(payload: SetCalloutStickerCodePayload) {
  return {
    type: SET_CALLOUT_STICKER_CODE,
    payload,
  };
}

export function setCalloutBackground(payload: SetCalloutBackgroundPayload) {
  return {
    type: SET_CALLOUT_BACKGROUND,
    payload,
  };
}

export function setCalloutSelected(payload: SetCalloutSelectedPayload) {
  return {
    type: SET_CALLOUT_SELECTED,
    payload,
  };
}

export function setMetaData(payload: SetMedataDataPayload) {
  return {
    type: SET_META_DATA,
    payload,
  };
}

export function clearCalloutSelected() {
  return {
    type: CLEAR_CALLOUT_SELECTED,
  };
}

export function convertKeysToContainer(payload: ConvertKeysPayload) {
  return {
    type: CONVERT_KEYS_TO_CONTAINER,
    payload,
  };
}

export function broadcastDataChange(obj: any) {
  const { sticker: sc, bgcolor: bg, border, showstk } = obj;
  const payload = { sc, bg, border, showstk };
  return {
    type: CALLOUT_DATA_CHANGED,
    payload,
  };
}
